<template>
  <div class="message">
    <div class="main">
      <div class="d-title">交友，吐槽，留言，建议</div>
      <div class="content">
        <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model.trim="message">
        </el-input>
        <div class="d-fourBlock"></div>
        <el-button type="primary" @click="fuckMessage">吐槽</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Message',
    data() {
      return {
        url: '/message',
        message: ''
      }
    },
    methods: {
      fuckMessage: function() {
        if (this.message != '') {
          // 设置需要post的值
          let message_data = {
            msg: this.message,
            username: this.$store.state.login_name
          }
          this.$axios.post(this.url + '/insert', message_data)
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.message,
                  type: 'success'
                })
              } else {
                this.$message({
                  message: res.data.message,
                  type: 'warning'
                })
              }
            })
        } else {
          this.$message({
            message: '吐槽内容可别忘填了',
            type: 'warning'
          })
        }
      }
    }
  }
</script>

<style>
</style>



